.react-clock {
  display: block;
  position: relative;
}
.react-clock,
.react-clock *,
.react-clock *:before,
.react-clock *:after {
  box-sizing: border-box;
}
.react-clock__face {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 1px solid black;
  border-radius: 50%;
}
.react-clock__hand {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 50%;
}
.react-clock__hand__body {
  position: absolute;
  background-color: black;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.react-clock__mark {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 50%;
}
.react-clock__mark__body {
  position: absolute;
  background-color: black;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.react-clock__mark__number {
  position: absolute;
  left: -40px;
  width: 80px;
  text-align: center;
}
.react-clock__second-hand__body {
  background-color: red;
}

.react-calendar {
  width: 350px;
  max-width: 100%;
  background: white;
  border: 1px solid #a0a096;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
}
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  height: 44px;
  margin-bottom: 1em;
}
.react-calendar__navigation button {
  min-width: 44px;
  background: none;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #e6e6e6;
}
.react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
}
.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;
}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
}
.react-calendar__month-view__weekNumbers {
  font-weight: bold;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
}
.react-calendar__month-view__days__day--weekend {
  color: red;
}
.react-calendar__month-view__days__day--neighboringMonth {
  color: #969696;
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.react-calendar__tile {
  max-width: 100%;
  text-align: center;
  padding: 0.75em 0.5em;
  background: none;
}
.react-calendar__tile:disabled {
  background-color: #f0f0f0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #e6e6e6;
}
.react-calendar__tile--hasActive {
  background: #76baff;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #a9d4ff;
}
.react-calendar__tile--active {
  background: #006edc;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #1087ff;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}

.chq-charts--wrap {
  display: inline-block;
  position: relative;
}

.chq-charts--export {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transition: opacity 300ms ease-in-out;
  transition: opacity 300ms ease-in-out;
}

.chq-charts--export svg {
  border-radius: 26px;
  box-sizing: content-box;
  height: 26px;
  margin: 0;
  padding: 4px;
  -webkit-transition: background-color 150ms;
  transition: background-color 150ms;
  width: 26px;
}

.chq-charts--export svg:hover {
  background-color: white;
}

.chq-charts--export button {
  background: inherit;
  border: 0;
  cursor: pointer;
  display: inline;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  outline: none;
  padding: 0;
}

.chq-charts--export-dropdown {
  border: 1px solid rgba(0, 0, 0, .15);
  border-radius: 4px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
  overflow: hidden;
  position: absolute;
  right: -5px;
  top: calc(100% + 3px);
  z-index: 1;
}

.chq-charts--export-dropdown a {
  background-color: white;
  color: #6a89af;
  display: block;
  padding: 5px 15px;
  text-decoration: none;
  white-space: nowrap;
}

.chq-charts--export-dropdown a:hover {
  background-color: #f7f7f7;
}

.chq-charts--export-trigger line {
  stroke: black;
  stroke-width: 1.5;
}

.chq-charts--wrap canvas {
  width: 400px;
  height: 400px;
}

.chq-charts--wrap:hover .chq-charts--export {
  opacity: 1;
  pointer-events: all;
}

.chq-charts--info {
  background-color: rgba(247, 247, 247, 0);
  box-sizing: border-box;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 27% 8px 8px;
  pointer-events: none;
  position: absolute;
  text-align: center;
  -webkit-transition: background-color 300ms ease-in-out, padding-top 400ms;
  transition: background-color 300ms ease-in-out, padding-top 400ms;
}

.chq-charts--info:focus {
  outline: none;
}

.chq-charts--info-show {
  background-color: rgba(247, 247, 247, .8);
  padding-top: 25%;
  pointer-events: all;
}

.chq-charts--mono {
  font-family: monospace;
}

.chq-charts--info button {
  background: none;
  border: 0;
  border-radius: 5px;
  color: #6a89af;
  cursor: pointer;
  display: inline;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  outline: none;
  padding: 4px 10px;
  -webkit-transition: background-color 300ms;
  transition: background-color 300ms;
}

.chq-charts--info button:focus,
.chq-charts--info button:hover {
  background-color: #f7f7f7;
  box-shadow: 0 0 1px rgba(0, 0, 0, .2);
}

.chq-charts--wrap .chq-charts--chart {
  overflow: visible;
}

.chq-charts--chart text,
.chq-charts--chart line {
  -webkit-animation: chqChartsFadeIn 1s ease-in-out forwards 1.25s;
          animation: chqChartsFadeIn 1s ease-in-out forwards 1.25s;
  opacity: 0;
}

.chq-charts--pie-slice {
  -webkit-animation: chqChartsFadeIn 1.5s ease-in-out forwards;
          animation: chqChartsFadeIn 1.5s ease-in-out forwards;
  cursor: pointer;
  opacity: 0;
  outline: none;
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}

.chq-charts--pie-slice:hover,
.chq-charts--pie-slice:focus {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.chq-charts--pie-slice path:first-child {
  opacity: .3;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}

.chq-charts--pie-slice:hover path:first-child,
.chq-charts--pie-slice:focus path:first-child {
  opacity: .6;
}

.chq-charts--hori-bar-group {
  -webkit-animation: chqChartsScaleX 1.5s ease-in-out forwards;
          animation: chqChartsScaleX 1.5s ease-in-out forwards;
  opacity: 0;
  outline: none;
  cursor: pointer;
  -webkit-transform: scale3d(0, 1, 1);
          transform: scale3d(0, 1, 1);
  transform-box: fill-box;
}

.chq-charts--vert-bar-group {
  -webkit-animation: chqChartsScaleY 1.5s ease-in-out forwards;
          animation: chqChartsScaleY 1.5s ease-in-out forwards;
  cursor: pointer;
  opacity: 0;
  outline: none;
  -webkit-transform: scale3d(1, 0, 1);
          transform: scale3d(1, 0, 1);
  transform-box: fill-box;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
}

.chq-charts--bar-shadow {
  opacity: 0;
  -webkit-transition: opacity 300ms ease-in-out;
  transition: opacity 300ms ease-in-out;
}

.chq-charts--hori-bar-group:hover .chq-charts--bar-shadow,
.chq-charts--hori-bar-group:focus .chq-charts--bar-shadow,
.chq-charts--vert-bar-group:hover .chq-charts--bar-shadow,
.chq-charts--vert-bar-group:focus .chq-charts--bar-shadow {
  opacity: .5;
}

.chq-charts--noselect {
  pointer-events: none;
}

@-webkit-keyframes chqChartsFadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes chqChartsFadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes chqChartsScaleX {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0, 1, 1);
            transform: scale3d(0, 1, 1);
  }

  to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@keyframes chqChartsScaleX {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0, 1, 1);
            transform: scale3d(0, 1, 1);
  }

  to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@-webkit-keyframes chqChartsScaleY {
  from {
    opacity: 0;
    -webkit-transform: scale3d(1, 0, 1);
            transform: scale3d(1, 0, 1);
  }

  to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@keyframes chqChartsScaleY {
  from {
    opacity: 0;
    -webkit-transform: scale3d(1, 0, 1);
            transform: scale3d(1, 0, 1);
  }

  to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

